ourcesContent":["// Variables\r\n$color-primary: #efa693;\r\n$color-secondary: #ffdcd3;\r\n$color-background: #caf0ff;\r\n$color-text-dark: #444950;\r\n$color-text-light: #939393;\r\n$font-family-main: \"Abhaya Libre\", serif;\r\n$font-family-alt: \"filson-pro\", sans-serif;\r\n\r\n// Mixins\r\n@mixin mobile {\r\n  @media screen and (max-width: 520px) {\r\n    @content;\r\n  }\r\n}\r\n\r\n@mixin centered_box($bg: white) {\r\n  background: $bg;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n// Global Styles\r\n* {\r\n  box-sizing: border-box;\r\n  font-family: $font-family-main;\r\n}\r\n\r\nbody {\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n// Footer\r\n.footer {\r\n  background: url(\"../assets/images/footer.png\") no-repeat;\r\n  background-size: cover;\r\n  color: #fff;\r\n  padding: 20px 0;\r\n  text-align: center;\r\n}\r\n\r\n// TitleHero\r\n.TitleHero {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n// TitlebgElement\r\n#TitlebgElement {\r\n  width: 70px;\r\n  height: 24px;\r\n  position: relative;\r\n  z-index: -1;\r\n  margin-top: -24px;\r\n  top: 63px;\r\n  left: -24px;\r\n  background: $color-background;\r\n  border-radius: 100px;\r\n  @media (max-width: 600px) {\r\n    left: 0;\r\n    top: 43px;\r\n  }\r\n}\r\n\r\n// ButtonBox\r\n.buttonBox {\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  gap: 10px;\r\n  margin-top: 60px;\r\n\r\n  @media (max-width: 600px) {\r\n    margin: auto auto auto 20px;\r\n    flex-direction: row !important;\r\n  }\r\n\r\n  button {\r\n    margin-top: auto;\r\n    margin-bottom: auto;\r\n    border: 0;\r\n    border-radius: 100px;\r\n    background-color: $color-secondary;\r\n    width: 70px;\r\n    height: 24px;\r\n    cursor: pointer;\r\n\r\n    div {\r\n      position: relative;\r\n      top: -10px;\r\n      left: 10px;\r\n      background-color: white;\r\n      border-radius: 100%;\r\n      width: 40px;\r\n      height: 40px;\r\n      box-shadow: 0px 4px 50px 0px #00000040;\r\n    }\r\n  }\r\n\r\n  @include mobile {\r\n    display: none;\r\n  }\r\n}\r\n\r\n// Header\r\n#header {\r\n  @include centered_box($color-secondary);\r\n  font-weight: 700;\r\n  font-size: 13px;\r\n  line-height: 18.2px;\r\n\r\n  p {\r\n    margin: 9px 0;\r\n  }\r\n}\r\n\r\n// Hero\r\n#hero {\r\n  color: $color-text-dark;\r\n\r\n  div {\r\n    display: flex;\r\n    flex-direction: row;\r\n    @media (max-width: 600px) {\r\n      flex-direction: column;\r\n    }\r\n\r\n    h2 {\r\n      font-size: 44px;\r\n      font-weight: 700;\r\n      line-height: 61.6px;\r\n      margin-left: auto;\r\n      margin-right: auto;\r\n      @media (max-width: 600px) {\r\n        margin-left: 20px;\r\n        font-size: 28px;\r\n        line-height: 35.84px;\r\n        text-align: left;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// CTA\r\n#cta {\r\n  @include centered_box();\r\n  margin: 72px 0;\r\n\r\n  @media (max-width: 600px) {\r\n    margin: 0 0 72px 0;\r\n  }\r\n\r\n  button {\r\n    cursor: pointer;\r\n    text-transform: uppercase;\r\n    padding: 12px 32px;\r\n    border: 0;\r\n    background: $color-primary;\r\n    color: white;\r\n    border-radius: 100px;\r\n    font-family: $font-family-alt;\r\n    font-size: 14px;\r\n    font-weight: 700;\r\n    line-height: 23.8px;\r\n    text-align: center;\r\n    transition: color 300ms;\r\n\r\n    &:hover {\r\n      background: white;\r\n      color: $color-primary;\r\n    }\r\n  }\r\n}\r\n\r\n// Subscription\r\n#subscription {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  margin: 220px 0 68px 0;\r\n\r\n  h2 {\r\n    margin: 16px 0;\r\n    max-width: 660px;\r\n    color: $color-text-dark;\r\n    font-size: 44px;\r\n    font-weight: 700;\r\n    line-height: 61.6px;\r\n    text-align: center;\r\n\r\n    @media (max-width: 600px) {\r\n      font-size: 28px;\r\n      line-height: 35.84px;\r\n    }\r\n  }\r\n\r\n  p {\r\n    max-width: 400px;\r\n    color: $color-text-light;\r\n    font-family: $font-family-alt;\r\n    font-size: 15px;\r\n    font-weight: 350;\r\n    line-height: 27px;\r\n    text-align: center;\r\n  }\r\n\r\n  form {\r\n    input {\r\n      border: 1px solid $color-primary;\r\n      height: 48px;\r\n      width: 360px;\r\n      border-radius: 99px;\r\n      padding: 6px 24px;\r\n      font-family: $font-family-alt;\r\n      font-size: 15px;\r\n      font-weight: 500;\r\n      line-height: 27px;\r\n      text-align: left;\r\n    }\r\n\r\n    button {\r\n      cursor: pointer;\r\n      border: 1px solid $color-primary;\r\n      height: 48px;\r\n      width: 120px;\r\n      margin-left: 10px;\r\n      color: white;\r\n      border-radius: 100px;\r\n      background: $color-primary;\r\n      text-transform: uppercase;\r\n      padding: 12px 40px;\r\n      font-family: $font-family-alt;\r\n      font-size: 14px;\r\n      font-weight: 700;\r\n      line-height: 23.8px;\r\n      text-align: center;\r\n      transition: color 300ms;\r\n\r\n      @media (max-width: 600px) {\r\n        margin-top: 10px;\r\n        width: 50%;\r\n      }\r\n\r\n      &:hover {\r\n        color: $color-primary;\r\n        background: white;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Carousel\r\n.product-card {\r\n  width: 320px;\r\n  scroll-snap-align: start;\r\n\r\n  @media (max-width: 600px) {\r\n    width: 243px;\r\n  }\r\n}\r\n\r\n#product-cards {\r\n  overflow-x: auto;\r\n  scroll-snap-type: x proximity;\r\n  scroll-behavior: smooth;\r\n  scrollbar-width: none;\r\n  display: grid;\r\n  grid-auto-flow: column;\r\n  gap: 16px;\r\n\r\n  &::-webkit-scrollbar {\r\n    display: none;\r\n  }\r\n\r\n  .dragging {\r\n    scroll-behavior: auto;\r\n    user-select: none;\r\n  }\r\n}\r\n\r\n.content {\r\n  user-select: none;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n\r\n  @media (max-width: 600px) {\r\n    max-width: 243px;\r\n  }\r\n\r\n  .imageContainer {\r\n    border-radius: 15px;\r\n    overflow: hidden;\r\n  }\r\n\r\n  img {\r\n    user-select: none;\r\n    max-width: 320px;\r\n    transform: scale(1.01);\r\n    overflow: hidden;\r\n  }\r\n\r\n  button {\r\n    cursor: pointer;\r\n    font-family: $font-family-alt;\r\n    position: relative;\r\n    width: -webkit-fill-available;\r\n    margin: 0 12px -40px;\r\n    top: -55px;\r\n    border: 0;\r\n    padding: 12px 3px;\r\n    background: white;\r\n    color: $color-primary;\r\n    border-radius: 100px;\r\n    opacity: 90%;\r\n    transition: color 300ms;\r\n\r\n    &:hover {\r\n      background: $color-primary;\r\n      color: white;\r\n    }\r\n  }\r\n\r\n  p {\r\n    margin-right: auto;\r\n    font-family: $font-family-alt;\r\n    font-size: 18px;\r\n    font-weight: 500;\r\n    line-height: 23.76px;\r\n    text-align: left;\r\n  }\r\n\r\n  div {\r\n    display: flex;\r\n    align-items: center;\r\n    width: -webkit-fill-available;\r\n\r\n    @media (max-width: 600px) {\r\n      flex-direction: column;\r\n    }\r\n\r\n    div {\r\n      color: #8a8a8a;\r\n      margin-left: 8px;\r\n      font-family: $font-family-alt;\r\n      font-size: 14px;\r\n      font-weight: 400;\r\n      line-height: 19.6px;\r\n      text-align: left;\r\n\r\n      @media (max-width: 600px) {\r\n        flex-direction: row;\r\n      }\r\n    }\r\n\r\n    p {\r\n      font-family: $font-family-alt;\r\n      font-size: 15px;\r\n      font-weight: 350;\r\n      line-height: 27px;\r\n      text-align: right;\r\n      margin: 0 0 0 auto;\r\n\r\n      @media (max-width: 600px) {\r\n        margin: 0 auto 0 12px;\r\n      }\r\n\r\n      span {\r\n        color: #8a8a8a;\r\n        text-decoration: line-through;\r\n        margin-right: 8px;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// Other styles\r\n.no-transition {\r\n  scroll-behavior: initial;\r\n}\r\n\r\n.dragging {\r\n  scroll-behavior: auto;\r\n  scroll-snap-type: none;\r\n}\r\n\r\n.starGold {\r\n  svg {\r\n    width: 17px;\r\n    margin: 0;\r\n    color: #ffc658;\r\n  }\r\n}\r\n\r\n.starGrey {\r\n  svg {\r\n    width: 17px;\r\n    margin: 0;\r\n    color: #939393;\r\n  }\r\n}\r\n\r\n#product-cards-container {\r\n  margin: 36px 0;\r\n}\r\n\r\n.angle {\r\n  margin: auto;\r\n}\r\n\r\n.flip {\r\n  transform: rotate(180deg);\r\n}\r\n"],"sourceRoot":""}]);
